import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import * as appActions from '../actions/appActions';
import { Route, Switch, withRouter } from 'react-router-dom';
import ContactPage from './contact/ContactPage';
import fontawesome from '@fortawesome/fontawesome';
import { faMoon, faTrophy, faArrowRight } from '@fortawesome/fontawesome-free-solid';
fontawesome.library.add(faMoon, faTrophy, faArrowRight); //eslint-disable-line import/no-named-as-default-member
import toastr from 'toastr';
toastr.options = {
    closeButton: true,
    closeDuration: 0,
    timeOut: 0,
    extendedTimeOut: 0,
    positionClass: 'toast-top-left'
};
import Header from './common/Header';
import AboutPage from './about/AboutPage';
import PortfolioPage from './portfolio/PortfolioPage';
import GamePage from './game/GamePage';

export class App extends React.Component {
    constructor(props) {
        super(props);
        this.changeTheme = this.changeTheme.bind(this);
    }
    componentDidMount() {

        document.body.className = this.props.app.theme;
    }
    changeTheme = (event) => {
        if (event.target.checked) {
            Promise.resolve(this.props.actions.changeTheme('theme-dark')).then(() => {
                document.body.className = this.props.app.theme;
            });
        } else {
            Promise.resolve(this.props.actions.changeTheme('theme-light')).then(() => {
                document.body.className = this.props.app.theme;
            });
        }
    }
    render() {
        return (
            <div>
                <Header loading={this.props.loading} changeTheme={this.changeTheme} darkTheme={this.props.app.darkTheme} />
                <div className="container-fluid main-container">
                    <Switch>
                        <Route exact path="/" component={AboutPage} />
                        <Route path="/portfolio" component={PortfolioPage} />
                        <Route path="/game" component={GamePage} />
                        <Route path="/contact" component={ContactPage} />
                    </Switch>
                </div>
            </div>
        );
    }
}

App.propTypes = {
    app: PropTypes.object,
    actions: PropTypes.object.isRequired,
    loading: PropTypes.bool
};

function mapStateToProps(state) {
    return {
        app: state.app,
        loading: state.ajaxCallsInProgress > 0
    };
}
function mapDispatchToProps(dispatch) {
    return {
        actions: bindActionCreators(appActions, dispatch)
    };
}

export default withRouter(connect(
    mapStateToProps,
    mapDispatchToProps
)(App));